
var nodes = [{ name: "桂林" }, { name: "广州" },
{ name: "厦门" }, { name: "杭州" },
{ name: "上海" }, { name: "青岛" },
{ name: "天津" }];

var links = [{ source: 0, target: 1 }, { source: 0, target: 2 },
{ source: 0, target: 3 }, { source: 1, target: 4 },
{ source: 1, target: 5 }, { source: 1, target: 6 }];

import * as d3 from 'd3';

export class Force {
    svg: d3.Selection<d3.BaseType, any, any, any>;
    g: d3.Selection<d3.BaseType, any, any, any>;
    zoom: d3.ZoomBehavior<d3.ZoomedElementBaseType, any>;
    nodes: Array<{ name: string }>;

    constructor(select: string) {
        this.nodes = nodes;
        this.svg = d3.select(select)
            .append("svg")
            .attr("width", innerWidth)
            .attr("height", innerHeight);
        this.doZoom();
        this.doDraw();
    }

    doZoom() {
        this.zoom = d3.zoom()
            .scaleExtent([0.5, 10])
            .duration(400)
            .on("zoom", () => {
                // if (!d3.event.active) {
                this.svg.select(".nodes").attr("transform", d3.event.transform);
                // }
            });
        this.svg.call(this.zoom);
    }

    doDraw() {
        let width = +this.svg.attr("width"),
            height = +this.svg.attr("height");

        let simulation = d3.forceSimulation(this.nodes)
            .force("link", d3.forceLink())
            .force("charge", d3.forceManyBody().strength(-150))
            .force("center", d3.forceCenter(width / 2, height / 2));

        var svg_texts = this.svg.selectAll("text")
            .data(nodes)
            .enter()
            .append("text")
            .style("fill", "black")
            .attr("dx", 20)
            .attr("dy", 8)
            .text(function (d) {
                return d.name;
            });

        let node = this.svg.append("g")
            .attr("class", "nodes")
            .selectAll("circle")
            .data(this.nodes)
            .enter()
            .append("circle")
            .attr("r", 50)
            .attr("cx", 100)
            .attr("cy", 100)
            .style("fill", "black")
            .call(d3.drag()
                .on("start", dragstarted)
                .on("drag", dragged)
                .on("end", dragended));

        simulation.nodes(this.nodes)
            .on("tick", ticked)
            .force("link");

        function dragstarted(d) {
            if (!d3.event.active) simulation.alphaTarget(0.5).restart();
            d.fx = d.x;
            d.fy = d.y;
        }

        function dragged(d) {
            d.fx = d3.event.x;
            d.fy = d3.event.y;
        }

        function dragended(d) {
            if (!d3.event.active) simulation.alphaTarget(0);
            d.fx = null;
            d.fy = null;
        }

        function ticked() {
            node.attr("cx", function (datum: d3.SimulationNodeDatum) { return datum.x; })
                .attr("cy", function (datum: d3.SimulationNodeDatum) { return datum.y; });
        }
    }
}


